        {label:'100-1000',value:'100-1000'}
      ],

    }
  },
  methods: {
    /**/
    planChange(file, fileList){
      if(file.status==="fail") {this.planButton=true;this.open(",");}
      else this.planButton=false;
    },
    planuploadsuccess(response, file, fileList){
      this.planList = fileList
    },
    planRemove(file, fileList) {
      this.planList = fileList.slice(1,1);
      if (fileList.length == 0) this.planButton = true;
      else this.planButton = true;

    },
    goBack(){//
      this.$router.go(-1);
    },
    /**/
    submitForm(formName) {
      let check=true
      this.$refs[formName].validate((valid) => {
        if (valid) {
          return
        } else {
          this.open('')
          check=false;
        }
      });
      return check;
    },
    /**/
    open(text) {
      this.$notify.error({
        message: text,
        offset: 300
      });
    },
    /**/
    open2(title,main,confirm,cancel) {
      this.$confirm(main,title , {
        confirmButtonText: confirm,
        cancelButtonText:cancel ,
        type: 'success'
      }).then(() => {
        this.$message({
          type: 'success',
          message: ''
        });
        this.$router.push({ name: 'editproject', query: {}})
      }).catch(() => {

        this.$router.push({ name: 'indexmyProject', query: {}})
      });
    },
    /**/
    allSave(){
      if(this.submitForm('project')) this.open2('','','','')
      console.log(this.$tool.getToObject(this.project));
    },
    /**/
    loadAll() {
      return [
        { "value": "", "address": "144" },
        { "value": "Hot honey ", "address": "661" },
        { "value": "", "address": "9886113" },
        { "value": "()", "address": "438" },
        { "value": "", "address": "96811818-101" },
        { "value": "", "address": "633" },
        { "value": "", "address": "1685" },
        { "value": "", "address": "1435" },
        { "value": "", "address": "144481B-107" },
        { "value": "", "address": "817" },
        { "value": "/", "address": "1611" },
        { "value": "", "address": "238355" },
        { "value": "Monica", "address": "24091F2383621F" },
        { "value": "soho", "address": "9689" },
        { "value": "NONO JUICE  ", "address": "119" }
      ];
    },
    /*,*/
    querySearchAsync(queryString, cb) {
      console.log(queryString)
      this.project.company=queryString;
      var restaurants = this.restaurants;
      var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 500 * Math.random());
    },
    createStateFilter(queryString) {
      return (state) => {
        return (state.value.indexOf(queryString.toLowerCase()) === 0);
      };
    },
    handleSelect(item) {
      console.log(item);
     this.dialogVisible=true;
    },
    /**/
    sync(){
      this.dialogVisible = false;
    }
  },
  mounted() {
    this.restaurants = this.loadAll();
  }

}
